import { ListItem, Content, Header, SoftKeys } from "./UI";
import { useHistory } from "react-router-dom";

export const Security = (props) => {
  const history = useHistory();
  const showAdvance = () => {
    history.push("/security");
  };

  let whenClicked = () => {
    history.goBack();
    props.toggleSecurity();
  };
  let isChecked = props.security === "open";
  return (
    <>
      <Header title="Security" />
      <Content>
        <ListItem
          text="None"
          type="radio"
          name="security"
          checked={isChecked}
          onClick={whenClicked}
        />
        <ListItem
          text="WPA-PSK2"
          type="radio"
          name="security"
          checked={!isChecked}
          onClick={whenClicked}
        />
        <SoftKeys />
      </Content>
    </>
  );
};
