export const Toast = (props) => {
  let toastStyle = {
    background: "#333333",
    padding: "1rem",
    position: "absolute",
    width: "100%",
    color: "white",
    textAlign: "center",
    zIndex: "1",
    boxShadow: "0 6px 6px rgba(0, 0, 0, 0.3)"
  };
  return (
    <div style={toastStyle}>
      <h1>{props.message}</h1>
    </div>
  );
};
