export const ListItem = (props) => {
  return (
    <div className="list-item focusable" tabindex="0" onClick={props.onClick}>
      {props.text ? <p className="list-item__text">{props.text}</p> : ""}
      {props.subtext ? (
        <p className="list-item__subtext">{props.subtext}</p>
      ) : (
        ""
      )}
      {props.indicator ? <span className="list-item__indicator"></span> : ""}
      {props.type ? <input
        name={props.name}
        type={props.type}
        checked={props.checked}
        tabindex="0"
        className={"list-item__" + props.type + " focusable"}
      /> : ""}
    </div>
  );
};
