import { Header, Separator, ListItem, SoftKeys, Content, Toast } from "./UI";
import { useHistory } from "react-router-dom";
export const Home = (props) => {
  const history = useHistory();
  const showAdvance = () => {
    history.push("/advance");
  };
  return (
    <>
      <Header title="Hotspot" />
      <Content>
        <ListItem
          text="On"
          type="radio"
          name="tethering"
          checked={props.tethering}
          onClick={props.toggleTethering}
        />
        <ListItem
          text="Off"
          type="radio"
          name="tethering"
          checked={!props.tethering}
          onClick={props.toggleTethering}
        />
        {props.tethering ? (
          <>
            <ListItem
              text="Advanced Options"
              indicator="true"
              onClick={showAdvance}
            />
            <Separator text="Info" />
            <ListItem text="Name" subtext={props.ssid} />
          </>
        ) : (
          ""
        )}
      </Content>
      <SoftKeys />
    </>
  );
};
