import { ListItem,Content,Header } from "./UI";
import { useHistory } from "react-router-dom";

export const Advance = (props) =>{
  const history = useHistory();
  const showSecurity = () => {
    history.push("/security");
  };

  let isOpen = props.security === "open";
  return(
    <>
    <Header title="Advance Options"/>
    <Content>
    <ListItem text="Security" indicator="true" subtext={props.security} onClick={showSecurity}/>
    {!isOpen ? <ListItem text="Password" indicator="true" subtext="********" onClick={props.showPasswordPrompt}
    /> :""}
    </Content>
    </>
  )
}