import { Component } from "react";
import { Advance, Home, Security } from "../components";
import { Toast } from "../components/UI";
import { Switch, Route } from "react-router-dom";
let lock = new Map();
if (navigator.mozSettings) {
  lock = navigator.mozSettings.createLock();
}
export class Layout extends Component {
  state = {
    toast: "",
    tethering: this.getLock("enabled"),
    ssid: this.getLock("ssid"),
    security: this.getLock("security.type"),
    password: this.getLock("security.password")
  };

  getLock(name) {
    let property = "tethering.wifi.".concat(name);
    return lock.get(property);
  }

  setLock(name, value) {
    let propertyName = "tethering.wifi.".concat(name);
    let property = {};
    property[propertyName] = value;
    return lock.set(property);
  }

  toggleTetheringHandler() {
    this.setLock("enabled", !this.state.tethering).onsucess(() => {
      this.setState((oldState) => {
        this.showToast("Hotspot turned ".concat(!oldState.tethering));
        return {
          ...oldState,
          tethering: !oldState.tethering
        };
      });
    });
  }

  PasswordPromptHandler() {
    let newPassword = prompt("Your Password", this.state.password);
    let changedPassword =
      newPassword !== null && newPassword !== this.state.password;
    if (changedPassword) {
      this.toggleTetheringHandler();
      this.setLock("security.password", newPassword).onsucess(() => {
        this.setState((oldState) => {
          return {
            ...oldState,
            password: newPassword
          };
        });

        this.showToast("Password Changed");
        this.toggleTetheringHandler();
      });
    }
  }

  toggleSecurityHandler() {
    let security = "open";
    if (this.state.security === "open") {
      security = "wpa2-psk";
    }
    this.toggleTetheringHandler();
    lock.set({ "tethering.wifi.security.type": security }).onsucess(() => {
      this.setState((oldState) => {
        return {
          ...oldState,
          security: security
        };
      });
      this.showToast("Changes Saved");
      this.toggleTetheringHandler();
    });
  }

  showToast(message) {
    this.setState((oldState) => {
      return {
        ...oldState,
        toast: message
      };
    });
    setTimeout(() => {
      this.setState((oldState) => {
        return {
          ...oldState,
          toast: ""
        };
      });
    }, 1500);
  }

  render() {
    return (
      <>
        {this.state.toast ? <Toast message={this.state.toast} /> : ""}
        <Switch>
          <Route exact path="/">
            <Home
              ssid={this.state.ssid}
              tethering={this.state.tethering}
              toggleTethering={() => {
                this.toggleTetheringHandler();
              }}
            />
          </Route>
          <Route exact path="/advance">
            <Advance
              security={this.state.security}
              showPasswordPrompt={() => {
                this.PasswordPromptHandler();
              }}
            />
          </Route>
          <Route exact path="/security">
            <Security
              security={this.state.security}
              toggleSecurity={() => {
                this.toggleSecurityHandler();
              }}
            />
          </Route>
        </Switch>
      </>
    );
  }
}
